@import "settings"
@import "_mixins/_mixins"
@import "_components/ui"
.main-products
  .container
    display: flex
    flex-direction: column
    align-items: center
    justify-content: flex-start
    height: 100%
  .btn-row
    margin-top: auto
    position: relative
    top: 7.5vh
  .products-wrapper
    position: relative
    width: 100%
    flex: 1 1 auto
    .product_image1
      position: absolute
      top: 15.5%
      left: 9.5%
    .product_image2
      position: absolute
      top: 2.5%
      right: 9%
    .product_image3
      position: absolute
      top: 63%
      left: 0
    .product_image4
      position: absolute
      top: 60.5%
      right: -5%
    .product_image5
      position: absolute
      top: 101%
      left: 23.5%
.main-quality
  &.swiper-slide
    .section-content
      padding: 12.75vh 0 0
      .container
        display: flex
        flex-direction: column
        align-items: center
        justify-content: flex-start
        height: 100%
  .quality-list
    display: flex
    align-items: center
    justify-content: space-between
    position: absolute
    width: calc(100% - 30px)
    bottom: 22px
    left: 15px
    background: rgba(52, 182, 180, 0.5)
    backdrop-filter: blur(5px)
    border-radius: 10px
    padding: 14px 0
    z-index: 2
    &__item
      flex: 0 0 auto
      width: auto
      margin: 0 auto
      .icon-block
        display: flex
        align-items: center
        justify-content: center
        .item-icon
          display: block
          width: auto
          max-width: 100%
          margin: 0 auto
      .descr-block
        display: none
  &__title
    position: relative
    z-index: 2
  &__content
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    .quality-slider
      max-height: 100vh
      .swiper-slide
        .slide-image
          display: block
          width: auto
          max-width: 100%
      .swiper-pagination
        display: flex
        align-items: center
        justify-content: flex-start
        overflow: hidden
        position: absolute
        bottom: 118px
        left: 15px
        width: 100%
        max-width: calc(100% - 30px)
        z-index: 2
        .swiper-pagination-bullet
          flex: 1 1 auto
          width: 100%
          margin: 0 2px 0
          background-color: $neutral-color
          height: 2px
          border-radius: 5px
          opacity: 1 !important
          transition: background-color .5s
          &.swiper-pagination-bullet-active
            background-color: $accent-color
    .btn-row
      position: absolute
      left: 0
      right: 0
      bottom: 135px
      z-index: 2
.main-popular
  .popular-list
    display: flex
    align-items: center
    justify-content: center
    margin-top: 25px
    &__item
      flex: 0 0 87px
      max-width: 87px
      text-align: center
      margin: 0 5px
    &__link
      background-color: rgba(18, 197, 185, 0.2)
      color: $accent-color
      font-weight: 400
      font-size: 14px
      line-height: 150%
      display: block
      width: 100%
      text-align: center
      padding: 9px 0
      border-radius: 50%
      transition: background-color .3s, color .3s
      &.active
        background-color: $accent-color
        color: #fff
  &__content
    max-width: calc(100% + 50px)
    margin: 30px -25px 0
    .popular-slider
      display: none
      padding-left: 20px
      .swiper-slide
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        background-color: #fff
        border-radius: 10px
        padding: 10px
        max-width: 278px
        margin-right: 15px
        .image-block
          flex: 0 0 auto
          width: 100%
          .slide-image
            display: block
            width: auto
            max-width: 100%
            margin: 0 auto
            border-radius: 10px
        .descr-block
          width: 100%
          display: flex
          align-items: flex-start
          justify-content: flex-start
          flex-direction: column
          flex: 1 1 auto
          padding: 10px 5px 5px
          .slide-title
            display: block
            font-family: Lineatura, sans-serif
            font-weight: 700
            font-size: 14px
            line-height: 130%
            color: #383838
          .slide-descr
            margin: 15px 0 10px
            font-size: 12px
            line-height: 150%
            color: #565656
          .slide-btns
            width: 100%
            display: flex
            align-items: center
            justify-content: flex-start
            flex-wrap: wrap
            margin-top: auto
            .more-link
              flex: 0 0 34px
              width: 34px
              height: 34px
              display: flex
              align-items: center
              justify-content: center
              border-radius: 50%
              background-color: $accent-color
              color: #fff
              box-shadow: 0px 4px 6px rgba(0, 13, 21, 0.25)
            .old-price
              display: inline-block
              flex: 0 0 auto
              width: auto
              margin: 0 10px 0 22px
              font-weight: 400
              font-size: 14px
              line-height: 100%
              color: #7D7D7D
              position: relative
              &:after
                content: ''
                display: block
                width: 100%
                height: 1px
                background-color: #7D7D7D
                position: absolute
                top: 50%
                left: 0
                transform: rotate(-15deg)
            .price
              display: inline-block
              flex: 0 0 auto
              width: auto
              margin: -4px 0 0 22px
              color: $accent-color
              font-family: Lineatura, sans-serif
              font-weight: 700
              font-size: 16px
              line-height: 100%
  .btn-row
    margin-top: 40px

.main-fathers
  &__title
    display: block
    position: absolute
    bottom: 12vh
    left: 0
    font-family: Lineatura, sans-serif
    font-weight: 700
    font-size: 14px
    line-height: 130%
    text-transform: uppercase
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.4)
    padding: 20px 15px
    max-width: 300px
    z-index: 2
    overflow: hidden
  &__content
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    .fathers-slider
      height: 100vh
      opacity: 0
      transition: opacity 2.5s
      .swiper-slide
        .image-block
          .slide-image
            display: block
            width: auto
            max-width: 100%
        .descr-block
          position: absolute
          bottom: 21.5vh
          left: 0
          padding: 0 15px
          font-family: Lineatura, sans-serif
          font-weight: 700
          font-size: 30px
          line-height: 130%
          text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
          span
            color: $accent-color
      .swiper-pagination
        display: flex
        align-items: center
        justify-content: flex-start
        overflow: hidden
        position: absolute
        bottom: 76px
        left: 15px
        width: 100%
        max-width: calc(100% - 30px)
        z-index: 2
        .swiper-pagination-bullet
          flex: 1 1 auto
          width: 100%
          margin: 0 2px 0
          background-color: $neutral-color
          height: 2px
          border-radius: 5px
          opacity: 1 !important
          transition: background-color .5s
          &.swiper-pagination-bullet-active
            background-color: $accent-color
  &.swiper-slide-active
    .main-fathers__content
      .fathers-slider
        opacity: 1
.main-lifehacks
  text-align: center
  &__subtitle
    display: inline-block
    text-transform: uppercase
    font-family: Lineatura, sans-serif
    font-weight: 700
    font-size: 16px
    line-height: 150%
    text-transform: uppercase
    text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.4)
    margin-top: 8px
    position: relative
    &:after
      content: url("../images/common/svg/arrow.svg")
      display: block
      position: absolute
      left: calc(100% + 40px)
      top: 3px
  &__content
    max-width: calc(100% + 50px)
    margin: 30px -25px 0
    .lifehacks-slider
      padding: 20px 30px 20px 30px
      .swiper-slide
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        background-color: #fff
        border-radius: 10px
        padding: 10px
        max-width: 261px
        margin-right: 30px
        transition: transform .3s
        &:nth-of-type(2n+1)
          transform: rotate(3deg)
        &:nth-of-type(2n)
          transform: rotate(-3deg)
        .image-block
          flex: 0 0 auto
          width: 100%
          .slide-image
            display: block
            width: auto
            max-width: 100%
            margin: 0 auto
            border-radius: 10px
        .descr-block
          width: 100%
          display: flex
          align-items: flex-start
          justify-content: flex-start
          flex-direction: column
          flex: 1 1 auto
          padding: 12px 10px 8px
          .slide-title
            display: block
            font-family: Lineatura, sans-serif
            font-weight: 700
            font-size: 16px
            line-height: 150%
            color: $bg-color
          .see-link
            display: inline-block
            font-weight: 400
            font-size: 14px
            line-height: 150%
            color: $bg-color
            position: relative
            margin: 5px 0 0 5px
            padding: 3px 0
            &:before
              content: ''
              display: block
              width: 100%
              height: 1px
              background-color: $accent-color
              transition: background-color .3s
              position: absolute
              bottom: -2px
              left: 0
            &:hover
              &:before
                background-color: $bg-color
      .swiper-pagination
        display: flex
        align-items: center
        justify-content: flex-start
        overflow: hidden
        position: relative
        margin: 55px -10px 0
        width: calc(100% + 20px)
        max-width: calc(100% + 20px)
        z-index: 2
        .swiper-pagination-bullet
          flex: 1 1 auto
          width: 100%
          margin: 0 2px 0
          background-color: $neutral-color
          height: 3px
          border-radius: 5px
          opacity: 1 !important
          transition: background-color .5s
          &.swiper-pagination-bullet-active
            background-color: $accent-color

.main-challenge
  &.swiper-slide
    .section-content
      padding: 12.75vh 0 0
  &__title
    display: inline-block
    position: relative
    &:after
      content: url("../images/common/svg/arrow2.svg")
      position: absolute
      top: calc(100% + 8px)
      left: 40%
  &__content
    margin-top: 90px
    &:after
      content: ""
      display: block
      width: 1311px
      height: 519px
      position: absolute
      top: calc(100% - 18vh)
      left: calc(-80% - 15px)
      background-image: url(../images/common/clouds.png)
      background-repeat: no-repeat
      background-size: cover
      background-position: center
      animation: fly_cloud 6s ease 0s infinite normal forwards
    .steps-list
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      max-width: 292px
      margin: 0 auto
      &__item
        display: flex
        align-items: center
        justify-content: flex-start
        margin-top: 56px
        &:nth-of-type(1)
          margin-top: 0
        &:nth-of-type(2n + 1)
          .number
            &:after
              content: url("../images/common/svg/step_arrow1.svg")
              display: block
              position: absolute
              top: calc(100% + 2px)
              right: calc(100% + 6px)
        &:nth-of-type(2n)
          .number
            &:after
              content: url("../images/common/svg/step_arrow2.svg")
              display: block
              position: absolute
              top: calc(100% + 8px)
              left: calc(100% - 12px)
        &:last-child
          .number
            &:after
              display: none
        .number
          display: flex
          align-items: center
          justify-content: center
          flex: 0 0 60px
          width: 60px
          color: #fff
          font-family: Lineatura, sans-serif
          font-weight: 700
          font-size: 16px
          line-height: 100%
          padding: 6px 10px 16px
          text-align: center
          position: relative
          background-color: $accent-color
          border-radius: 50%
        p
          flex: 0 0 calc(100% - 60px)
          max-width: calc(100% - 60px)
          padding-left: 18px
          font-size: 14px
          line-height: 150%
    .btn-row
      margin: 40px 0

@import "node_modules/swiper/swiper-bundle"
@import "_components/footer"
@import "_custom/modalMagnific"
@import "_components/modals"
@import "_animates/_animates"