@import "../settings"
.section-title
  font-family: Lineatura, sans-serif
  font-weight: 700
  font-size: 24px
  line-height: 150%
  text-align: center
  text-transform: uppercase
  text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.4)
  span
    color: $accent-color
.btn-row
  display: flex
  align-items: center
  justify-content: center
.btn-style
  display: inline-block
  background-color: $btn-accent-color
  font-family: Exo2, sans-serif
  font-weight: 400
  font-size: 14px
  line-height: 17px
  color: #fff
  text-transform: uppercase
  box-shadow: 0px 8px 15px rgba($btn-accent-color, 0.55)
  border-radius: 10px
  padding: 18px 40px
  transition: background-color .3s, box-shadow .3s
  border: none
  &:hover
    background-color: $btn-green-color
    box-shadow: 0px 8px 15px rgba($btn-green-color, 0.55)
.input-style
  display: block
  width: 100%
  background-color: transparent
  border: 1px solid #D5D5D5
  border-radius: 50px
  font-size: 12px
  line-height: 150%
  padding: 13px 22px 14px
  transition: border-color .3s
  color: #fff
  &:focus
    border-color: $accent-color
  @include input-placeholder
    color: #fff
.file-group
  label
    position: relative
    .label
      display: flex
      align-items: center
      justify-content: flex-start
      padding: 0 5px
      .icon-file
        display: inline-block
        color: $accent-color
        font-size: 20px
        line-height: 20px
        margin-right: 15px
      span
        font-size: 12px
        line-height: 150%
        text-decoration: underline
    input
      position: absolute
      top: 0
      left: 0
      width: 0
      height: 0
      opacity: 0
      visibility: hidden
    &:hover
      .label
        span
          text-decoration: none
.radio-group
  label
    display: block
    position: relative
    input
      position: absolute
      top: 0
      left: 0
      width: 0
      height: 0
      opacity: 0
      visibility: hidden
      &:checked
        & + .label
          border-color: $accent-color
          &:before
            border-color: $accent-color
    .label
      display: block
      width: 100%
      background-color: transparent
      border: 1px solid #D5D5D5
      border-radius: 50px
      font-size: 12px
      line-height: 150%
      padding: 13px 22px 14px 44px
      transition: border-color .3s
      color: #fff
      position: relative
      &:before
        content: ''
        display: block
        width: 16px
        height: 16px
        background-color: #fff
        border-radius: 50%
        position: absolute
        top: 15px
        left: 15px
        border: 4px solid transparent
        transition: border-color .3s

.check-group
  label
    display: block
    position: relative
    text-align: left
    input
      position: absolute
      top: 0
      left: 0
      width: 0
      height: 0
      opacity: 0
      visibility: hidden
      &:checked
        & + .label
          &:before
            border-color: $accent-color
            color: $accent-color
    .label
      display: inline-block
      padding-left: 42px
      font-weight: 400
      font-size: 10px
      line-height: 150%
      position: relative
      &:before
        display: flex
        align-items: center
        justify-content: center
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        content: '\e802'
        color: transparent
        width: 18px
        height: 18px
        border-radius: 3px
        border: 1px solid #fff
        font-size: 8px
        line-height: 8px
        position: absolute
        top: -1px
        left: 5px
        transition: color .3s, border-color .3s
      a:hover
        text-decoration: none

