.modal-style
  height: 100vh
  padding: 50px 20px
  display: flex
  align-items: center
  justify-content: center
  flex-direction: column
  &:before
    content: ''
    display: block
    width: 100%
    height: 100vh
    position: fixed
    top: 0
    left: 0
    background: radial-gradient(50% 50% at 50% 50%, #036C75 0%, #012238 100%)
  .modal-content
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    flex: 1 1 100%
    height: 100%
    width: 100%
    &:before
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      top: 0
      left: 0
      background-image: url(../images/common/stars_1.png)
      background-position: top left
      animation: stars 3s ease 0s infinite normal forwards
    &:after
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      top: 0
      left: 0
      background-image: url(../images/common/stars_2.png)
      background-position: top left
      animation: stars_hide 3s ease infinite normal forwards
    & > div
      position: relative
      z-index: 1
  .modal-title
    display: block
    font-family: Lineatura, sans-serif
    font-weight: 700
    font-size: 18px
    line-height: 150%
    text-align: center
    text-transform: uppercase
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
    margin-bottom: 40px
    width: 100%
  .modal-form-wrapper
    width: 100%
  .modal-form
    width: 100%
    .input-group
      margin-top: 10px
      &:nth-of-type(1)
        margin-top: 0
      &.file-group
        margin-top: 25px
      &.btn-group
        text-align: center
        margin-top: 40px
  .modal-message
    display: none
    width: 100%
    text-align: center
    .message-title
      font-family: Lineatura, sans-serif
      font-weight: 700
      font-size: 24px
      line-height: 150%
      text-transform: uppercase
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
      span
        color: $accent-color
    .icon
      display: flex
      align-items: center
      justify-content: center
      width: 76px
      height: 76px
      border-radius: 50%
      background-color: rgba($accent-color, .95)
      color: #fff
      margin: 45px auto 38px
      box-shadow: 0px 10px 5px rgba(0, 0, 0, 0.25), 0px 10px 50px rgba(255, 255, 255, 0.25)
      font-size: 28px
      line-height: 28px
    .message-txt
      font-size: 14px
      font-weight: 400
      line-height: 150%
      b
        font-weight: 400
        font-size: 12px
        line-height: 150%
      p
        margin-top: 20px
        &:nth-of-type(1)
          margin-top: 0
    .test-link
      margin-top: 40px
.modal-quiz-style
  min-height: 100vh
  padding: 50px 20px 0
  display: flex
  align-items: center
  justify-content: center
  flex-direction: column
  &:before
    content: ''
    display: block
    width: 100%
    height: 100vh
    position: fixed
    top: 0
    left: 0
    background: radial-gradient(50% 50% at 50% 50%, #036C75 0%, #012238 100%)
  .modal-content
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    flex: 1 1 100%
    height: 100%
    width: 100%
    &:before
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      top: 0
      left: 0
      background-image: url(../images/common/stars_1.png)
      background-position: top left
      animation: stars 3s ease 0s infinite normal forwards
    &:after
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      top: 0
      left: 0
      background-image: url(../images/common/stars_2.png)
      background-position: top left
      animation: stars_hide 3s ease infinite normal forwards

    & > div
      position: relative
      z-index: 1
    &__step
      display: none
      height: 100%
      .step-title
        display: block
        width: 100%
        font-family: Lineatura, sans-serif
        font-weight: 700
        font-size: 18px
        line-height: 150%
        text-align: center
        text-transform: uppercase
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
        margin-bottom: 35px
      .step-wrapper
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        height: 100%
      .gift-block
        margin: auto auto 30px
        position: relative
        .gift-image
          display: block
          width: auto
          max-width: 100%
          margin: 20px auto 0
        .gift-value
          display: flex
          align-items: center
          justify-content: center
          flex-wrap: wrap
          text-align: center
          position: absolute
          bottom: 0
          left: 0
          right: 0
          margin: 0 auto 18px
          width: 96px
          height: 96px
          border-radius: 50%
          background-color: rgba(#27979D, .95)
          box-shadow: 0px 10px 5px rgba(0, 0, 0, 0.25), 0px 10px 50px rgba(255, 255, 255, 0.25)
          color: #fff
          font-size: 12px
          line-height: 14px
          text-transform: uppercase
          span
            display: block
            font-family: Lineatura, sans-serif
            font-weight: 700
            font-size: 24px
            line-height: 100%
            margin: -5px 0 8px

      .btn-row
        display: flex
        align-items: flex-start
        justify-content: flex-start
        width: 100%
        padding-bottom: 20px
        .back-link
          display: flex
          align-items: center
          justify-content: center
          flex: 0 0 42px
          width: 42px
          height: 42px
          border: 1px solid $accent-color
          color: $accent-color
          background-color: transparent
          border-radius: 50%
          transition: background-color .3s, color .3s
          .icon
            transform: scale(-1,1)
            font-size: 16px
            line-height: 16px
          &:hover
            background-color: $accent-color
            color: #fff
        .next-link
          font-family: Inter, sans-serif
          display: inline-flex
          flex: 0 0 auto
          width: auto
          margin: 0 0 0 auto
          background-color: $accent-color
          color: #fff
          border-radius: 30px
          font-size: 14px
          line-height: 100%
          padding: 14px 20px 14px 42px
          text-transform: uppercase
          .icon
            margin-left: 22px
            font-size: 16px

      .input-group
        width: 100%
        margin-top: 10px
        &:nth-of-type(1)
          margin-top: 0
      .input-wrapper
        display: flex
        align-items: stretch
        justify-content: flex-start
        flex-wrap: wrap
        width: 100%
        margin: 0 auto
        .case-group
          flex: 0 0 calc(50% - 8px)
          max-width: calc(50% - 8px)
          margin: 0 16px 15px 0
          label
            display: block
            position: relative
            .case-image
              display: block
              width: 100%
              max-width: 100%
              margin: 0
            input
              position: absolute
              top: 0
              left: 0
              width: 0
              height: 0
              opacity: 0
              visibility: hidden
              &:checked
                & + .label
                  color: $accent-color
                  border-color: $accent-color
            .label
              display: block
              border: 1px solid #fff
              border-radius: 10px
              overflow: hidden
              transition: border-color .3s, color .3s
              span
                display: block
                padding: 10px 10px 14px
                text-align: center
                font-size: 12px
                line-height: 150%


          &:nth-of-type(2n)
            margin-right: 0
        .radio-group
          width: 100%
          flex: 0 0 100%
          max-width: 100%
      &[data-step="1"]
        text-align: center
        .modal-title
          font-family: Lineatura, sans-serif
          font-weight: 700
          font-size: 24px
          line-height: 150%
          text-align: center
          text-transform: uppercase
          text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
          span
            color: $accent-color
        .modal-subtitle
          display: block
          font-weight: 400
          font-size: 14px
          line-height: 150%
          text-align: center
          margin-top: 30px
        .test-link
          margin-top: 32px
        .rocket-image
          display: block
          margin: 15vh auto 0
          max-width: 100%
        .info-link
          display: inline-block
          margin-top: 45px
          color: #fff
          font-size: 12px
          line-height: 150%
          text-decoration: underline
          &:hover
            text-decoration: none
      &[data-step="3"],
      &[data-step="4"],
      &[data-step="5"]
        .step-title
          font-size: 24px
          line-height: 150%
      &[data-step="61"],
      &[data-step="62"],
      &[data-step="63"],
      &[data-step="64"]
        .input-wrapper
          max-width: 294px
      &[data-step="7"]
        text-align: center
        .step-wrapper
          align-items: center
          justify-content: center
          height: auto
        .modal-title
          font-family: Lineatura, sans-serif
          font-weight: 700
          font-size: 24px
          line-height: 150%
          text-align: center
          text-transform: uppercase
          text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
          span
            color: $accent-color
        .modal-subtitle
          display: block
          font-family: Lineatura, sans-serif
          font-weight: 700
          font-size: 14px
          line-height: 130%
          margin-top: 20px
        .input-wrapper
          margin-top: 45px
          .input-group
            margin-top: 10px
            &:nth-of-type(1)
              margin-top: 0
            &.btn-group
              margin-top: 42px
              margin-bottom: 38px
            &.check-group
              margin-top: 0
        .modal-message
          display: none
          text-align: center
          .message-title
            font-family: Lineatura, sans-serif
            font-weight: 700
            font-size: 24px
            line-height: 150%
            text-transform: uppercase
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
            span
              color: $accent-color
          .gift-block
            margin: 45px 0
            .gift-value
              position: relative
              width: 106px
              height: 106px
          .message-txt
            font-size: 14px
            font-weight: 400
            line-height: 150%
            b
              font-weight: 400
              font-size: 12px
              line-height: 150%
            p
              margin-top: 20px
              &:nth-of-type(1)
                margin-top: 0
          .test-link
            margin-top: 45px
          .info-link
            display: inline-block
            margin-top: 45px
            color: #fff
            font-size: 12px
            line-height: 150%
            text-decoration: underline
            &:hover
              text-decoration: none
