  border: 1px solid #D5D5D5;
  border-radius: 50px;
  font-size: 12px;
  line-height: 150%;
  padding: 13px 22px 14px;
  transition: border-color 0.3s;
  color: #fff;
}
.input-style:focus {
  border-color: rgb(88, 220, 227);
}
.input-style.placeholder {
  color: #fff;
}
.input-style::-webkit-input-placeholder {
  color: #fff;
}
.input-style::-moz-placeholder {
  color: #fff;
}
.input-style:-moz-placeholder {
  color: #fff;
}
.input-style:-ms-input-placeholder {
  color: #fff;
}

.file-group label {
  position: relative;
}
.file-group label .label {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 5px;
}
.file-group label .label .icon-file {
  display: inline-block;
  color: rgb(88, 220, 227);
  font-size: 20px;
  line-height: 20px;
  margin-right: 15px;
}
.file-group label .label span {
  font-size: 12px;
  line-height: 150%;
  text-decoration: underline;
}
.file-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}
.file-group label:hover .label span {
  text-decoration: none;
}

.radio-group label {
  display: block;
  position: relative;
}
.radio-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}
.radio-group label input:checked + .label {
  border-color: rgb(88, 220, 227);
}
.radio-group label input:checked + .label:before {
  border-color: rgb(88, 220, 227);
}
.radio-group label .label {
  display: block;
  width: 100%;
  background-color: transparent;
  border: 1px solid #D5D5D5;
  border-radius: 50px;
  font-size: 12px;
  line-height: 150%;
  padding: 13px 22px 14px 44px;
  transition: border-color 0.3s;
  color: #fff;
  position: relative;
}
.radio-group label .label:before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 15px;
  left: 15px;
  border: 4px solid transparent;
  transition: border-color 0.3s;
}

.check-group label {
  display: block;
  position: relative;
  text-align: left;
}
.check-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}
.check-group label input:checked + .label:before {
  border-color: rgb(88, 220, 227);
  color: rgb(88, 220, 227);
}
.check-group label .label {
  display: inline-block;
  padding-left: 42px;
  font-weight: 400;
  font-size: 10px;
  line-height: 150%;
  position: relative;
}
.check-group label .label:before {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  content: "\e802";
  color: transparent;
  width: 18px;
  height: 18px;
  border-radius: 3px;
  border: 1px solid #fff;
  font-size: 8px;
  line-height: 8px;
  position: absolute;
  top: -1px;
  left: 5px;
  transition: color 0.3s, border-color 0.3s;
}
.check-group label .label a:hover {
  text-decoration: none;
}

