.footer
  width: 100%
  margin-top: auto
  text-align: center
  font-size: 12px
  line-height: 150%
  padding: 15px 0
  position: relative
  z-index: 2
  .social-list
    display: flex
    align-items: center
    justify-content: center
    margin: 20px 0
    &__item
      flex: 0 0 auto
      width: auto
      margin: 0 30px
    &__link
      display: inline-block
      color: $accent-color
      transition: transform .3s
      font-size: 16px
      line-height: 26px
      filter: drop-shadow(0px 6px 4px rgba(0, 0, 0, 0.4))
      .icon
        &.icon-yt
          font-size: 26px
        &.icon-ok
          font-size: 24px
      &:hover
        transform: translateY(-5px)
  .doc-link
    display: inline-block
    margin-top: 10px
    color: #fff
    position: relative
    &:before
      content: ''
      display: block
      width: 0
      height: 1px
      background-color: $accent-color
      transition: width .3s
    &:hover
      &:before
        width: 100%